.App {
    text-align: center;
    font-family: 'Roboto', sans-serif;
}

.renders{
    width: 100%;
    height: 100vh;
    height: var(--height-renders);
}

.menu-low{
    width: 100%;
    height: 10vh;
    height: var(--height-menu-low);
