.x, .x-i, .x-f{
    position: fixed;
    background-image: url(/IndomoDesignArt/static/media/cross.964706aa.png);
    background-size: cover;
    background-repeat: no-repeat;
    background-color: rgb(255, 255, 255);
    border-radius: 50%;
    left: 92vw;
    top: 3vh;
    z-index: 998;
    transition: 150ms;
    transition-delay: 100ms;
    transition-duration: 500ms;
}
.x{
    width: 0vw;
    width: var(--x-width);
    height: 5vw;
}
.x-i{
    width: 0vw;
    width: var(--xi-width);
    height: 5vw;
}
.x-f{
    width: 0vw;
    width: var(--xf-width);
    height: 5vw;
