.services{
    position: fixed;
    width: 100vw;
    height: 28vh;
    bottom: 15vh;
    background-color: rgba(0, 255, 17, 0);
    z-index: 7;
    display: flex;
    transition-delay: 450ms;
    transition-duration: 500ms;
}
.diseno, .muebles, .luminarias{
    width: 20%;
    height: 25vh;
    background-color: rgba(5, 5, 253, 0);
