.video-fondo video{
    visibility: 'hidden';
    visibility: var(--show-fondo-s);
    position: absolute;
    width: 100vw;
    height: 100vh;
    z-index: -10;
    z-index: var(--z-fondo);
    object-fit: cover;
    background-color: rgb(0, 0, 0);
    left: 0px;
    bottom: 0%;
    transition: 6000ms;
    transition-delay: 100ms;
    transition-duration: 5500ms; 
}

